package com.devscion.classy.db

import app.cash.sqldelight.Query
import app.cash.sqldelight.TransacterImpl
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.Long
import kotlin.String

public class ImageQueries(
  driver: SqlDriver,
) : TransacterImpl(driver) {
  public fun <T : Any> getAllImages(mapper: (
    id: Long,
    imageBase64: String,
    date: String,
    prompt: String,
  ) -> T): Query<T> = Query(-740_906_081, arrayOf("image"), driver, "Image.sq", "getAllImages",
      "SELECT * FROM image") { cursor ->
    mapper(
      cursor.getLong(0)!!,
      cursor.getString(1)!!,
      cursor.getString(2)!!,
      cursor.getString(3)!!
    )
  }

  public fun getAllImages(): Query<Image> = getAllImages { id, imageBase64, date, prompt ->
    Image(
      id,
      imageBase64,
      date,
      prompt
    )
  }

  public fun insert(image: Image) {
    driver.execute(1_381_492_373,
        """INSERT INTO image (imageBase64,date,prompt) VALUES (?, ?, ?)""", 3) {
          bindString(0, image.imageBase64)
          bindString(1, image.date)
          bindString(2, image.prompt)
        }
    notifyQueries(1_381_492_373) { emit ->
      emit("image")
    }
  }
}
