package com.devscion.classy.db

import app.cash.sqldelight.Transacter
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import com.devscion.classy.db.shared.newInstance
import com.devscion.classy.db.shared.schema
import kotlin.Unit

public interface Classy : Transacter {
  public val imageQueries: ImageQueries

  public companion object {
    public val Schema: SqlSchema<QueryResult.Value<Unit>>
      get() = Classy::class.schema

    public operator fun invoke(driver: SqlDriver): Classy = Classy::class.newInstance(driver)
  }
}
