package com.devscion.classy.db.shared

import app.cash.sqldelight.TransacterImpl
import app.cash.sqldelight.db.AfterVersion
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import com.devscion.classy.db.Classy
import com.devscion.classy.db.ImageQueries
import kotlin.Long
import kotlin.Unit
import kotlin.reflect.KClass

internal val KClass<Classy>.schema: SqlSchema<QueryResult.Value<Unit>>
  get() = ClassyImpl.Schema

internal fun KClass<Classy>.newInstance(driver: SqlDriver): Classy = ClassyImpl(driver)

private class ClassyImpl(
  driver: SqlDriver,
) : TransacterImpl(driver), Classy {
  override val imageQueries: ImageQueries = ImageQueries(driver)

  public object Schema : SqlSchema<QueryResult.Value<Unit>> {
    override val version: Long
      get() = 1

    override fun create(driver: SqlDriver): QueryResult.Value<Unit> {
      driver.execute(null, """
          |CREATE TABLE image (
          |  id INTEGER PRIMARY KEY NOT NULL,
          |  imageBase64 TEXT NOT NULL,
          |  date TEXT NOT NULL,
          |  prompt TEXT NOT NULL
          |)
          """.trimMargin(), 0)
      return QueryResult.Unit
    }

    override fun migrate(
      driver: SqlDriver,
      oldVersion: Long,
      newVersion: Long,
      vararg callbacks: AfterVersion,
    ): QueryResult.Value<Unit> = QueryResult.Unit
  }
}
